@import "../../styles/variables.scss";

.error {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 30px;
  color: $mainColor;

  &__text {
    font-size: 20px;
    font-weight: 500;

    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }
}
