@import "../../styles/variables.scss";

.trans {
  padding: 5px 15px;
  font-size: 24px;
  text-transform: uppercase;
  border: 2px solid $mainColor;
  border-radius: $borderRadius;
  color: $mainColor;
  transition: all 0.3s ease-in-out;
  cursor: pointer;

  &:hover {
    background-color: $mainColor;
    color: $hoverColor;
  }

  &:focus {
    outline-color: $hoverColor;
  }
}
