@import "../../styles/variables.scss";

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

.logo {
  width: 50px;
  height: 50px;
}

.main {
  flex: 1 1 auto;
  margin: 0 auto;
  padding: 0 15px;
  padding-top: 100px;
  max-width: 1300px;
}

@media (max-width: 280px) {
  .logo {
    width: 40px;
    height: 40px;
  }
}
